(function() {
  'use strict';

  angular
    .module('noteApp')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('noteApp')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('noteApp')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('noteApp')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(NetworkFactory,toastr,$compile,$scope) {
    var vm = this;

    angular.extend(vm, {
      NoteLists:[],
      NoteListsAvailable:false,
      Pagination:[],
      note:{}
    });


    vm.submitNoteForm = function(isValid) {
      // check to make sure the form is completely valid
      if (isValid) {
          var payload = {};
          if(vm.note && vm.note !== null || vm.note !== undefined) {
              payload=vm.note;
              NetworkFactory.addNote(payload).then(function (res) {
                  console.log(res);
                  if (res && res.success == true && res.data) {
                      toastr.success('Note successfully added/updated.');
                  }else{
                      toastr.error('Note unable to add/update.');
                  }
                  vm.getNoteLists();
                  vm.note = {};
              });
          }
      }
    };

    vm.deleteNote = function(id) {
          if(id!== null || id !== undefined) {
              NetworkFactory.deleteNote(id).then(function (res) {
                  console.log(res);
                  if (res && res.success == true && res.data) {
                      toastr.success('Note successfully deleted.');
                  }else{
                      toastr.error('Unable to delete note.');
                  }
                  vm.getNoteLists();
              });
          }
    };


    vm.editNote = function(id) {
      if(id!== null || id !== undefined) {
          NetworkFactory.editNote(id).then(function (res) {
              console.log(res);
              if (res && res.success == true && res.data) {
                  vm.note.title=res.data.title;
                  vm.note.body=res.data.body;
                  vm.note.id=res.data.id;
              }

          });
      }
    };

    vm.getNoteLists = function () {
      vm.NoteListsAvailable=false;
      NetworkFactory.getNoteLists().then(function (NoteLists) {
        if(NoteLists && NoteLists.success == true && NoteLists.data) {
          vm.NoteListsAvailable=true;
          vm.NoteLists = NoteLists.data;
          vm.Pagination=NoteLists.pagination;
          console.log(vm.NoteLists);
        }else{
          vm.NoteLists =[];
          vm.Pagination=[];
        }
      })
    };

    /*INIT function  here  */
    var init = (function () {
      vm.getNoteLists();
    })();
  }
})();

